@import "../../css/shared.scss";

.p-return{
  padding: 0 px(35) px(120) px(35);

  .circle{
    position: relative;
    width: 100%;
    height: px(768);
    margin: px(90) auto 0 auto;
    z-index: 1;
    text-align: center;

    background: url('../../images/circle.png') center 0 no-repeat;
    background-size: contain;

    & > div{
      width: px(420);
      height: px(520);
      margin: 0 auto;
      position: relative;
      top: px(103);
      //background: red;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }

    .p1{
      font-size: px(45);
      //width: px(420);
      border-bottom: 1px solid #fff;
      margin: px(100) auto 0 auto;
      line-height: px(106);
      padding: 0 px(30);
    }

    .p2{
      font-size: px(55);
      color: #ffd75e;
      margin-top: px(30);
      margin-bottom: px(120);
      word-break: break-all;

      i{
        font-style: normal;
        font-size: px(58);
      }
    }
  }

  .list{
    //margin: px(50) 0 0 0;
    margin-top: px(50);
    padding: 0;

    li{
      padding: px(40) 0;
      border-bottom: px(3) solid #5a089c;
      border-image: $linear-gradient 1 10;

      & > div{
        display: inline-block;
        text-align: center;
        font-size: px(45);

        &:nth-of-type(1){
          width: 28%;
        }
        &:nth-of-type(2){
          width: 26%;
          color: #ffd75e;
        }
        &:nth-of-type(3){
          width: 26%;
          color: #ffd75e;
        }
        &:nth-of-type(4){
          width: 20%;

          button{
            color: #fff;
            background: $linear-gradient;
            border: 0;
            border-radius: px(10);
            padding: px(5) px(25);
          }
        }
      }
    }
  }

}
